import React from "react"
import "../styles/style.css"
import MyHelmet from "../components/my-helmet"

export default function Home() {
  return (
    <div className={"flex flex-col justify-around h-full w-full"}>
      <div
        className={"flex flex-col sm:flex-row sm:items-center justify-between"}
      >
        <div
          className={
            "h-full w-full flex-grow flex flex-col flex-grow items-center justify-center"
          }
        >
          <h1 className={"text-4xl mt-4 sm:mt-0 sm:text-5xl text-gray-300"}>
            404 – Not Found
          </h1>
          <h2 className={"text-3xl mt-4 sm:mt-0 text-gray-300 text-center"}>
            Are you looking for the{" "}
            <a href={"/"} className={"text-pistachio underline"}>
              home page
            </a>{" "}
            or one of the following:
          </h2>
        </div>
      </div>
      <div
        className={
          "flex flex-col sm:flex-row sm:justify-evenly items-center justify-between"
        }
      >
        <a
          className={"text-gray-300 text-2xl sm:w-1/6"}
          href={"//github.com/fcFn"}
        >
          Peter Timoshevsky's GitHub
        </a>
        <a
          className={"text-gray-300 text-2xl my-4 sm:w-1/6"}
          href={"//blog.timoshevsky.com"}
        >
          Peter Timoshevsky's Blog
        </a>
        <a
          className={"text-gray-300 text-2xl sm:w-1/6"}
          href={"//www.linkedin.com/in/ptimoshevsky/"}
        >
          Peter Timoshevsky's LinkedIn
        </a>
      </div>
      <a
        href={"https://theuselessweb.com/"}
        className={"mx-auto text-gray-300 text-2xl"}
      >
        ?
      </a>
    </div>
  )
}

export function Head() {
  return (
    <MyHelmet>
      <title>404 – Not Found</title>
    </MyHelmet>
  )
}
